.product{
    padding: 20px 50px;
    display: flex;
    gap: 50px;
     
    .left{
        flex: 1;

        .images{
            flex: 1;

            img{
                width: 100%;
                height: 150px;
                object-fit:cover ;
                cursor: pointer;
                margin-bottom: 10px;
            }

        }
        .mainImg{
            flex: 5;


        }

    }

    .right{
        flex: 1;


    }
}